	module.exports = {"data":{"markdownRemark":{"html":"<p>Ethan Zuckerman has a <a href=\"http://www.ethanzuckerman.com/blog/\">great post</a> about the recent newsines (trendiness?) about \"conflict\" diamonds, pointing to a parody site <a href=\"http://realdiamondfacts.org/\">realdiamondfacts.org</a>. It's a sendup of DeBeers and Co. (It's an exact parody of their PR-campaign website, <a href=\"http://www.diamondfacts.org/\">diamondfacts.org</a>.)</p>\n<img src=\"/static/images/unofficial.jpg\" alt=\"Unofficial\">\n<p>The bigger issue, Zuckerman points out, is that there are any number of products that the Rich Folks of the world are consuming that cause economic and social trauma in the same way that the diamond industry does (<a href=\"http://news.bbc.co.uk/2/hi/africa/1468772.stm\">Coltran</a> — used in my cellphone — seems to be worse than diamonds if you ask me ... ).</p>","frontmatter":{"created_at":"December 11, 2006","path":"/2006/12/11/blood-diamonds-in-the-news","title":"Blood Diamonds in the News","tags":["politics","africa","trauma"]}}},"pathContext":{}}

